import { observer } from 'mobx-react-lite'
import React from 'react'
import { Link } from 'react-router-dom'
import { Container, Header, Segment, Image, Button, Divider } from 'semantic-ui-react'
import { useStore } from '../../app/stores/store'
import LoginForm from '../users/LoginForm'
import RegisterForm from '../users/RegisterForm'

export default observer(function HomePage() {
  const { userStore, modelStore } = useStore()

  return (
    <Segment inverted textAlign='center' vertical className='masthead'>
      <Container text>
        <Header as='h1' inverted>
          <Image
            size='massive'
            src='/assets/logo.png'
            alt='logo'
            style={{ marginBottom: 12 }}
          />
          GTC
        </Header>
        {userStore.isLoggedIn ? (
          <>
            <Header as='h2' inverted content='Welcome to Goma Tech Connect' />
            <Header as='h3' inverted content={`Let's skyrock together`} />
            <Button as={Link} to='/activities' size='huge' inverted>
              Go to Activities
            </Button>
          </>
        ) : (
          <>
            <Button
              onClick={() => modelStore.openModal(<LoginForm />)}
              size='huge'
              inverted>
              Login!
            </Button>
            <Button
              onClick={() => modelStore.openModal(<RegisterForm />)}
              size='huge'
              inverted>
              Register!
            </Button>
            <Divider horizontal inverted>Or</Divider>
            <Button 
              loading={userStore.fbLoading}
              size='huge' 
              content='Login with Facebook'
              color='facebook'
              inverted
              onClick={userStore.facebookLogin}
            />              
          </>
        )}
      </Container>
    </Segment>
  )
})
